import { createRouter, createWebHistory } from 'vue-router';

const routes = [
    {
        path: '/',
        name: 'Home',
        component: () => import('../views/HelloWorld.vue')
    },
    {
        path: '/gallerys',
        name: 'Gallery',
        component: () => import('../views/gallery/index.vue')
    },
    {
        path: '/gallerys/works',
        name: 'Work',
        component: () => import('../views/gallery/work/index.vue')
    },
    {
        path: '/gallerys/works/errfiles',
        name: 'Errfile',
        component: () => import('../views/gallery/work/errfile/index.vue')
    },
    {
        path: '/list',
        name: 'video_list',
        component: () => import('../views/video/list.vue')
    },
    {
        path: '/video',
        name: 'video',
        component: () => import('../views/video/video.vue')
    },
    {
        path: '/season',
        name: 'season',
        component: () => import('../views/video/season.vue')
    },
    {
        path: '/player',
        name: 'player',
        component: () => import('../views/video/player.vue')
    },
    {
        path: '/person',
        name: 'person',
        component: () => import('../views/video/person.vue')
    },
    {
        path: '/heart',
        name: 'heart',
        component: () => import('../views/user/heart.vue')
    },
    {
        path: '/star',
        name: 'star',
        component: () => import('../views/user/star.vue')
    },
    {
        path: '/played',
        name: 'played',
        component: () => import('../views/user/played.vue')
    },
    {
        path: '/users',
        name: 'users',
        component: () => import('../views/user/index.vue')
    },
    {
        path: '/search',
        name: 'search',
        component: () => import('../views/video/search.vue')
    },
    {
        path: '/console',
        name: 'console',
        component: () => import('../views/user/console.vue')
    },
    {
        path: '/setting',
        name: 'setting',
        component: () => import('../views/setting/index.vue')
    },
];

const router = createRouter({
    history: createWebHistory(),
    routes,
    // linkActiveClass: 'active',
    scrollBehavior(to, from, savedPosition) {
        return { x: 0, y: 0 }
    }
});


router.afterEach((to, from, next) => {
    window.scrollTo(0, 0);
});


export default router