<script type="text/javascript">
// 定义一些公共的属性和方法
let title = 'OneList';
// const apiUrl = 'http://127.0.0.1:8002';
let apiUrl = process.env.NODE_ENV === 'production' ? "" : 'http://127.0.0.1:8002';
let imgUrl = "https://image.tmdb.org"
const isMo = /Android|webOS|iPhone|iPad|iPod|BlackBerry/i.test(navigator.userAgent);

function LoadConfig(data) {
    imgUrl = data.imgUrl;
    console.log("加载配置")
}

// 暴露出这些属性和方法
export default {
    apiUrl,
    title,
    isMo,
    imgUrl,
    LoadConfig,
}
</script>